/* eslint-disable */
/**
 * useOrderData -      
 *   OC-   IT- 
 */

import { useState, useEffect, useCallback } from 'react'
import axios from 'axios'

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || ''

export function useOrderData(orderId, options = {}) {
  const {
    loadIssueCard = false,
    loadItems = true,
    loadDocuments = false,
    onError
  } = options
  
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [order, setOrder] = useState(null)
  const [items, setItems] = useState([])
  const [issueCard, setIssueCard] = useState(null)
  const [documents, setDocuments] = useState([])
  
  //   
  const orderSource = order?.order_number?.startsWith('IT-') 
    ? 'event_tool' 
    : order?.order_number?.startsWith('OC-') 
      ? 'opencart' 
      : 'manual'
  
  const isEventToolOrder = orderSource === 'event_tool'
  
  //  
  const fetchOrder = useCallback(async () => {
    if (!orderId) return
    
    setLoading(true)
    setError(null)
    
    try {
      const response = await axios.get(`${BACKEND_URL}/api/orders/${orderId}`)
      const orderData = response.data
      setOrder(orderData)
      
      //   items  
      if (loadItems && orderData.items) {
        setItems(orderData.items)
      } else if (loadItems) {
        //  
        const itemsRes = await axios.get(`${BACKEND_URL}/api/orders/${orderId}/items`)
        setItems(itemsRes.data || [])
      }
      
      //  issue card  
      if (loadIssueCard && orderData.issue_card_id) {
        try {
          const icRes = await axios.get(`${BACKEND_URL}/api/orders/${orderId}/issue-card`)
          setIssueCard(icRes.data)
        } catch (e) {
          console.warn('Issue card not found')
        }
      }
      
      //  
      if (loadDocuments) {
        try {
          const docsRes = await axios.get(`${BACKEND_URL}/api/orders/${orderId}/documents`)
          setDocuments(docsRes.data || [])
        } catch (e) {
          console.warn('Documents not found')
        }
      }
      
    } catch (err) {
      console.error('Error loading order:', err)
      setError(err.response?.data?.detail || '  ')
      onError?.(err)
    } finally {
      setLoading(false)
    }
  }, [orderId, loadIssueCard, loadItems, loadDocuments, onError])
  
  //  
  const updateOrder = useCallback(async (updates) => {
    if (!orderId) return
    
    try {
      const response = await axios.patch(`${BACKEND_URL}/api/orders/${orderId}`, updates)
      setOrder(prev => ({ ...prev, ...response.data }))
      return response.data
    } catch (err) {
      console.error('Error updating order:', err)
      throw err
    }
  }, [orderId])
  
  //  item
  const updateItem = useCallback(async (itemId, updates) => {
    if (!orderId) return
    
    try {
      const response = await axios.patch(`${BACKEND_URL}/api/orders/${orderId}/items/${itemId}`, updates)
      setItems(prev => prev.map(item => 
        item.id === itemId ? { ...item, ...response.data } : item
      ))
      return response.data
    } catch (err) {
      console.error('Error updating item:', err)
      throw err
    }
  }, [orderId])
  
  //  item
  const addItem = useCallback(async (itemData) => {
    if (!orderId) return
    
    try {
      const response = await axios.post(`${BACKEND_URL}/api/orders/${orderId}/items`, itemData)
      setItems(prev => [...prev, response.data])
      return response.data
    } catch (err) {
      console.error('Error adding item:', err)
      throw err
    }
  }, [orderId])
  
  //  item
  const removeItem = useCallback(async (itemId) => {
    if (!orderId) return
    
    try {
      await axios.delete(`${BACKEND_URL}/api/orders/${orderId}/items/${itemId}`)
      setItems(prev => prev.filter(item => item.id !== itemId))
    } catch (err) {
      console.error('Error removing item:', err)
      throw err
    }
  }, [orderId])
  
  // 
  const refresh = useCallback(() => {
    fetchOrder()
  }, [fetchOrder])
  
  useEffect(() => {
    fetchOrder()
  }, [fetchOrder])
  
  return {
    // State
    loading,
    error,
    order,
    items,
    issueCard,
    documents,
    
    // Computed
    orderSource,
    isEventToolOrder,
    
    // Actions
    setOrder,
    setItems,
    updateOrder,
    updateItem,
    addItem,
    removeItem,
    refresh
  }
}

/**
 *  notes  Ivent-tool 
 * Notes   
 */
export function parseEventToolNotes(notes) {
  if (!notes) return {}
  
  const parsed = {
    source: null,
    deliveryType: null,
    deliveryAddress: null,
    eventName: null,
    eventType: null,
    guestsCount: null,
    setupRequired: false,
    setupNotes: null,
    payerCompany: null,
    payerEdrpou: null,
    customerComment: null
  }
  
  const lines = notes.split('\n')
  
  for (const line of lines) {
    if (line.includes('[: Ivent-tool]')) {
      parsed.source = 'event_tool'
    } else if (line.startsWith(':')) {
      parsed.deliveryType = line.replace(':', '').trim()
    } else if (line.startsWith(':')) {
      parsed.deliveryAddress = line.replace(':', '').trim()
    } else if (line.startsWith(':')) {
      parsed.deliveryAddress = line.replace(':', '').trim()
    } else if (line.startsWith(' :')) {
      parsed.eventName = line.replace(' :', '').trim()
    } else if (line.startsWith(' :')) {
      parsed.eventType = line.replace(' :', '').trim()
    } else if (line.startsWith(' :')) {
      parsed.guestsCount = parseInt(line.replace(' :', '').trim())
    } else if (line.startsWith(' : ')) {
      parsed.setupRequired = true
    } else if (line.startsWith(' :')) {
      parsed.setupNotes = line.replace(' :', '').trim()
    } else if (line.startsWith(':')) {
      parsed.payerCompany = line.replace(':', '').trim()
    } else if (line.startsWith(':')) {
      parsed.payerEdrpou = line.replace(':', '').trim()
    } else if (line.startsWith(' :')) {
      parsed.customerComment = line.replace(' :', '').trim()
    }
  }
  
  return parsed
}

export default useOrderData
